export default {
  data () {
    return {
      goodsList: []
    }
  },
  onShow () {
    // 商品列表数据重置
    this.goodsList = []
    this.getGoodsList()
    wx.setTabBarBadge({
      index: 2,
      text: Object.keys(this.$store.getters.getCart).length + ''
    })
  },
  onHide () {
    // 取goodsList的购物车的数据，覆盖storage cart
    this.$store.commit('updateCart', this.goodsList)
  },
  computed: {
    isAll: {
      get () {
        // 所有的商品都选中的话，就是全选
        return this.goodsList.every(v => v.checked)
      },
      set (status) {
        // console.log(status)
        // 所有商品的checkbox状态和全选一致
        this.goodsList.forEach(v => {
          v.checked = status
        })
      }
    },
    totalNum () {
      // 所有选中的商品数量之和
      /*
      let sum = 0
      this.goodsList.forEach(v => {
        if (v.checked) {
          sum += v.num
        }
      })
      return sum
      */
      return this.goodsList.reduce(function (sum, v) {
        return sum + (v.checked ? v.num : 0)
      }, 0)
    },
    totalPrice () {
      return this.goodsList.reduce(function (sum, v) {
        return sum + (v.checked ? v.num * v.goods_price : 0)
      }, 0)
    }
  },
  methods: {
    toggleAll(){
      this.isAll =!this.isAll
    },
    add(item){
      item.num++
    },
    sub(item){
      item.num--
    },
    toggleCheck(item){
      item.checked=!item.checked
    },
    toPay () {
      // 没有商品就提示
      if (!this.totalNum) {
        wx.showToast({
          title: '请添加商品',
          icon: 'none'
        })
        return
      }
      // 没有token，就跳转登录
      let token = wx.getStorageSync('token')
      if (!token) {
        wx.navigateTo({ url: '/pages/login/main' })
      }
      // 满足以上条件，跳转支付
      wx.navigateTo({ url: '/pages/pay/main' })
    },
    async getGoodsList () {
      let cart = this.$store.getters.getCart || {}
      let ids = Object.keys(cart).join(',')
      // 购物车数据为空的，不必请求
      if (!ids) {
        return
      }
      let goodsList = await this.$request({
        url: '/api/public/v1/goods/goodslist?goods_ids=' + ids
      })
      // cart和goodsList数据融合
      goodsList.forEach(v => {
        v.num = cart[v.goods_id].num
        v.checked = cart[v.goods_id].checked
      })

      this.goodsList = goodsList
    }
  }
}
